import { useFrame } from '@react-three/fiber';
import { useControls } from 'leva';
import React, { PropsWithChildren, useCallback, useState } from 'react';
import { Vector3 } from 'three';
import { useMouse } from '../utils/useMouse';
import { Character } from './Character';
import { Level } from './Level';

interface GameSceneProps { }

export const GameScene: React.FC<GameSceneProps> = ({
  children,
}: PropsWithChildren<GameSceneProps>) => {
  const mouseUp = useMouse(['Left']);
  const [moving, setMoving] = useState(false);

  const {
    followMouse,
  } = useControls(
    {
      followMouse: false,
    },
  );

  // –¢—É—Ç —á—Ç–æ-—Ç–æ –Ω–µ —Ç–∞–∫ —Å –∫–æ–ª–ª–±—ç–∫–∞–º–∏. followMouse –∏ moving –Ω–µ —Å–±—Ä–∞—Å—ã–≤–∞—é—Ç –º–µ–º–æ–∏–∑–∞—Ü–∏—é –∫–æ–ª–ª–±—ç–∫–∞.
  // –ß—Ç–æ-—Ç–æ —è –Ω–µ –ø–æ–Ω–∏–º–∞—é –¥–æ –∫–æ–Ω—Ü–∞ –≤ —Ä–µ–∞–∫—Ç–µ —Ç—É—Ç, –∑–∞–º—ã–∫–∞–Ω–∏—è –Ω–µ –∑–∞–º—ã–∫–∞—é—Ç—Å—è.
  // –ê –µ—Å–ª–∏ followMouse true –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é —Å–¥–µ–ª–∞—Ç—å, —Ç–æ —Ç–æ–ª—å–∫–æ –æ–¥–∏–Ω —Ä–∞–∑ —Ä–∞–±–æ—Ç–∞–µ—Ç.

  const [moveTarget, setMoveTarget] = useState<Vector3 | null>(null);

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const onTerrainPointerDown = useCallback((target: Vector3) => {
    if (followMouse) {
      setMoving(true);
    }
    setMoveTarget(target);
  }, [followMouse, moving]);

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const onTerrainPointerMove = useCallback((target: Vector3) => {
    if (followMouse && moving) {
      setMoveTarget(target);
    }
  }, [followMouse, moving]);

  useFrame(() => {
    if (mouseUp && moving) {
      setMoving(false);
    }
  });

  return (
    <>
      <Level />
      <pointLight color="white" intensity={2} position={[10, 20, 5]} />
      <ambientLight color="yellow" intensity={0.2} />
      <Character moveTarget={moveTarget} />
      {children}
    </>
  );
};
